.btn-main {
  position: relative;
  display: block;
  padding: 0;
  border: 0 none;
  width: 28px;
  height: 18px;
  cursor: pointer;
  outline: none;
  background-color: transparent;
  z-index: 55;
}

.btn-main__line {
  display: block;
  position: absolute;
  width: 28px;
  height: 2px;
  background-color: #1A202D;
  left: 50%;
  margin-left: -14px;
  transition: .4s;
}

.btn-main__line:first-child {
  top: 0;
}

.btn-main__line:nth-child(2) {
  top: 50%;
  margin-top: -1px;
}

.btn-main__line:last-child {
  bottom: 0;
}

.btn-main.open span:first-child {
  top: 50%;
  margin-top: -1px;
  transform: rotate(225deg);
  background-color: #fff;
}

.btn-main.open span:nth-child(2) {
  transform: scale(0);
}

.btn-main.open span:last-child {
  top: 50%;
  margin-top: -1px;
  transform: rotate(-225deg);
  background-color: #fff;
}

.btn-mode {
