.btn-close {
  display: block;
  position: relative;
  padding: 0;
  border: 0 none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  overflow: hidden;
  text-indent: -9999px;
  width: 20px;
  height: 20px;
}

.btn-close:before, .btn-close:after {
  position: absolute;
  content: '';
  width: 26px;
  height: 2px;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -13px;
}

.btn-close:before {
  transform: rotate(135deg);
}

.btn-close:after {
  transform: rotate(45deg);
}

.btn-close--white:before, .btn-close--white:after {
  background-color: #fff;
}

.btn-collapse {
  display: block;
  position: relative;
  width: 10px;
  height: 16px;
  padding: 0;
  border: 0 none;
  background-color: transparent;
  overflow: hidden;
  text-indent: -9999px;
  outline: none;
  cursor: pointer;
}

.btn-collapse.open span {
  transform: scale(-1);
}

.btn-collapse span {
  position: absolute;
  display: block;
  width: 10px;
  height: 7px;
  left: 0;
  right: 0;
}

.btn-collapse span:first-child {
  top: 0;
}

.btn-collapse span:first-child:before {
  transform: rotate(45deg);
}

.btn-collapse span:first-child:after {
  transform: rotate(-45deg);
}

.btn-collapse span:last-child {
  bottom: 0;
}

.btn-collapse span:last-child:before {
  transform: rotate(135deg);
}

.btn-collapse span:last-child:after {
  transform: rotate(-135deg);
}

.btn-collapse span:before, .btn-collapse span:after {
  position: absolute;
  content: '';
  width: 2px;
  height: 7px;
  background-color: #F37032;
}

.btn-collapse span:before {
  left: 50%;
  margin-left: -3px;
}

.btn-collapse span:after {
  right: 50%;
  margin-right: -3px;
}

