[data-anim] {
  position: relative;
}

[data-anim-main-container] {
  position: absolute;
  top: 71px;
  left: 0;
  width: 100%;
  height: calc(100vh - 70px);
  overflow: hidden;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
}

[data-anim-main-container].is-animate {
  position: relative;
  top: auto;
  overflow: visible;
  visibility: visible;
  opacity: 1;
  z-index: 1;
  height: auto;
}

[data-anim-main-container] .main {
  width: 100%;
}

[data-anim-main].is-animate {
  position: absolute;
  top: 71px;
  left: 0;
  width: 100%;
  height: calc(100vh - 70px);
  overflow: hidden;
}

[data-anim-main-fade-in] {
  opacity: 0;
  transform: translateY(5px);
}

[data-anim-flip] {
  transition: 0.45s;
  transform-style: preserve-3d;
}

[data-anim-flip].is-flip {
  transform: rotateX(180deg);
  pointer-events: none;
}

[data-anim-flip-front] {
  backface-visibility: hidden;
}

[data-anim-flip-back] {
  backface-visibility: hidden;
  transform: rotateX(180deg);
}

.is-flip [data-anim-flip-back] {
  z-index: 2;
  pointer-events: initial;
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.25s, visibility 0.25s, z-index 0.25s;
  background-color: rgba(26, 32, 45, 0.5);
  padding: 25px 0;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
  overflow-x: hidden;
}

[data-modal] .slider-btn--next {
  right: -55px;
}

[data-modal] .slider-btn--prev {
  left: -55px;
}

[data-modal] .slick-dots {
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 991px) {
  [data-modal] .slick-dots {
    position: absolute;
    bottom: -45px;
    left: 0;
    right: 0;
  }
}

@media (max-width: 767px) {
  [data-modal] .slick-dots {
    bottom: -45px;
  }
}

@media (min-width: 992px) {
  [data-modal] .slick-dots {
    display: none !important;
    visibility: hidden;
  }
}

[data-modal] .slick-dots li {
  padding-left: 5px;
  padding-right: 5px;
}

[data-modal] .slick-dots li.slick-active button {
  background-color: #F37032;
  cursor: default;
  pointer-events: none;
}

[data-modal] .slick-dots button {
  display: block;
  width: 10px;
  height: 10px;
  padding: 0;
  border: 0 none;
  border-radius: 50%;
  background-color: #D9D0C8;
  cursor: pointer;
  outline: none;
  font-size: 0;
  transition: background-color .4s;
}

[data-modal] .slick-dots button:hover {
  background-color: #1A202D;
}

[data-modal] .slick-slide {
  perspective: 1000px;
  height: auto;
}

[data-modal] .slick-list {
  overflow: visible !important;
}

@media (max-width: 767px) {
  [data-modal] .slick-list {
    overflow: hidden !important;
  }
}

[data-modal] .slick-list, [data-modal] .slick-track {
  transition: height .3s;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 101;
}

[data-modal-container] {
  position: relative;
  max-width: 770px;
  width: 100%;
  transform: scale(0.7);
  z-index: 1;
  text-align: left;
  transition: transform 0.3s ease-out;
  vertical-align: middle;
  display: inline-block;
  margin: 0 auto;
}

[data-modal].is-open [data-modal-container] {
  transform: scale(1);
}

[data-tabs] {
  overflow: hidden;
}

[data-tabs] [data-tabs-control] {
  cursor: pointer;
  user-select: none;
}

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled {
  cursor: not-allowed;
}

[data-tabs].is-tabs-init [data-tabs-container] {
  display: block;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s, visibility 0.4s;
}

[data-tabs].is-tabs-init [data-tabs-container].is-open {
  opacity: 1;
